import React from "react";
import logo from "../images/logo.png";
import { Link } from "react-router-dom";
import NavIconToggleTheme from "../images/theme-toggle.png";
function Navbar(props) {
	const {
		theme,
		selectedTheme,
		toggleAppThemeChange,
		toggleShowNavbarMoreOptions,
		showNavbarMoreOptions,
	} = props;

	const toggleThemeContainerClass = {
		true: "d-flex",
		false: "d-none",
	};
	return (
		<nav
			style={{ background: theme.color }}
			className="navbar w-100  mb-5 px-xl-3 px-lg-3 px-md-3 px-sm-3 px-1  d-flex justify-content-between align-items-center"
		>
			<Link to="/todo-home">
				<div className="w-100  d-flex justify-content-start align-items-center">
					<img src={logo} className="navbar__logo" alt="Todo money logo" />

					<h1 className="navbar__brand-name m-0 ">
						<span className="navbar__brand-name--first-child">TODO</span>&nbsp;
						<span className="navbar__brand-name--second-child">Money</span>
					</h1>
				</div>
			</Link>
			<div className="nav-theme-toggler">
				<button
					onClick={() => props.toggleShowNavbarMoreOptions(true)}
					style={{ color: selectedTheme == "light" ? "#0e0e0e" : "#fff" }}
					className="navbar-more-options-btn"
				>
					<img
						src={NavIconToggleTheme}
						className="theme-toggler-icon-navbar mb-2 "
					/>
				</button>
				<div
					style={{
						backgroundColor: selectedTheme == "light" ? "#1a1a1a" : "#fff",
					}}
					className={
						toggleThemeContainerClass[showNavbarMoreOptions] +
						" flex-column toggle-theme-label-btn-container p-3 justify-content-center align-items-start"
					}
				>
					<div className="close-theme-toggler-container-header w-100">
						<button
							type="button"
							onClick={() => toggleShowNavbarMoreOptions(false)}
							className="close-toggle-theme-container-btn d-flex justify-content-center align-items-center"
						>
							<span className="font-weight-light">&times;</span>
						</button>
					</div>

					<p
						className="mb-2 text-left w-100 navbar-theme-toggler-label"
						style={{ color: selectedTheme == "light" ? "#fff" : "#0e0e0e" }}
					>
						Toggle{" "}
						<strong>{selectedTheme == "light" ? "Dark" : "Light"}</strong> theme
					</p>
					<input
						onChange={(e) => toggleAppThemeChange(e.target.checked)}
						type="checkbox"
						className="navbar-theme-toggler"
					/>
				</div>
			</div>
		</nav>
	);
}

export default Navbar;



