import React, { Component, createContext } from "react";

export const UserAuth = createContext();

class UserAuthProvider extends Component {
	constructor(props) {
		super(props);
		this.state = {
			name: "Vaibhav",
		};
	}
	render() {
		return (
			<UserAuth.Provider value={{ ...this.state }}>
				{this.props.children}
			</UserAuth.Provider>
		);
	}
}

export default UserAuthProvider;



